<template>

  <el-main style="background-color: #000000;color:white;font-family: 'Noto Sans SC';font-weight: 300;">


    <el-row type="flex" justify="center" style="width:100%;">
      <el-col :xs="24" :sm="24" :md="18":xl="18">
        <el-row>
          <el-col :xs="24" :sm="10">
          <el-row type="flex" justify="flex-start">
            <div :span="8" style="margin-top:10px;text-align:left;">
              <img style="text-align:left;width:66px;"
                   src="cfx-logo.svg"/>
            </div>
            <div :span="16"
                 style="text-align:left;color:#ffffff;font-size:18px;margin-top:20px;margin-left:20px;">

              <div style="display: inline-block;">
                {{ $t('poolSentence4') }}
              </div>
              <div style="color:#E12968;font-weight: 400;">
                ${{ poolMoney }}
              </div>
            </div>
          </el-row>
          </el-col>

          <el-col :xs="24" :sm="{span:14}">

            <el-row style="margin-top:20px;">
              <el-col :xs="{span:11}" :sm="{span:8,offset:7}">
                <vs-button block @click.stop="showDepositDialog"
                           style="font-size:20px;line-height:40px;background:linear-gradient(90deg, #E91E63, #FC9E7C);"
                           color="#E91E63">
                  {{$t('deposit')}}
                </vs-button>
              </el-col>
              <el-col :xs="{span:11,offset:2}" :sm="{span:8,offset:1}">
                <vs-button block @click.stop="showWithdrawDialog"
                           border style="font-size:20px;line-height:40px;"
                           color="#E91E63">
                  {{$t('withdraw')}}
                </vs-button>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <div
        style="padding-top:-80px;padding-bottom:300px;background:url('home-bg-2.png');background-repeat:no-repeat;background-size:100%;background-position:right;width:100%;">
    </div>

    <el-row type="flex" justify="center" style="margin-top:-270px;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <!--          <img src="second-home-bg.png" style="width:100%;">-->
        <div class="shadow-border">
          <el-row style="margin-bottom:10px;">
            <el-col :xs="24" :sm="24":lg="12">
              <div style="color:#ffffff;">

                <el-row type="flex" justify="center">
                  <el-col :xs="24" :md="20" style="line-height:60px;">
                    <div class="prize-shadow-border" style="margin-top:40px;margin-bottom: 20px;">
                      <el-row type="flex" justify="center">
                        <div  style="margin-top:10px;text-align:right;">
                          <img class="img-width"style="text-align:right;"
                               src="cfx-logo.svg"/>
                        </div>
                        <div
                             style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">

                          <div class="money-text"
                              style="margin-left:20px;text-align:left;font-weight: 800;display: inline-block;color: #FFFFFF;background: linear-gradient(77.7398deg, #C9225B 0.9521484375%, #FF3278 60.6689453125%, #FC9E7C 100%);-webkit-background-clip: text;-webkit-text-fill-color: transparent; letter-spacing:8px">
                            ${{ poolAwardMoney }}
                          </div>
                        </div>
                      </el-row>
                      <el-row>
                        <div style="margin-top:-10px;padding-bottom: 10px;">
                          {{ $t('poolsSentence2') }}
                        </div>
                      </el-row>

                    </div>
                  </el-col>
                </el-row>
              </div>
            </el-col>
            <el-col :xs="24" :sm="24" :lg="12">
              <div style="margin-bottom: 30px;">
                <el-row  >
                  <div  class="prize-text">
                    {{$t('poolSentence1') }}
                  </div>
                </el-row>
                <el-row type="flex" justify="center" style="margin-top:20px">
                  <el-col :span="24">
                    <circular-count-down-timer
                        :initial-value="0"
                        :stroke-width="3"
                        :seconds-stroke-color="'#ffffff'"
                        :minutes-stroke-color="'#ffffff'"
                        :hours-stroke-color="'#ffffff'"
                        :days-stroke-color="'#ffffff'"
                        :underneath-stroke-color="'#E91E63'"
                        :seconds-fill-color="'rbga(#ffffff,0)'"
                        :minutes-fill-color="'rbga(#ffffff,0)'"
                        :hours-fill-color="'rbga(#ffffff,0)'"
                        :days-fill-color="'rbga(#ffffff,0)'"
                        :size="countdownTimerWidth"
                        :padding="countdownTimerPadding"
                        :day-label="$t('days')"
                        :hour-label="$t('hours')"
                        :minute-label="$t('minutes')"
                        :second-label="$t('seconds')"
                        :show-second="true"
                        :show-minute="true"
                        :show-hour="true"
                        :show-negatives="false"
                        :paused="false"
                        :notify-every="'minute'"
                        style="color:#ffffff;"
                        ref="timer"
                    ></circular-count-down-timer>
                  </el-col>
                </el-row>

              </div>
            </el-col>

          </el-row>
        </div>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" style="margin-top:40px;background:url('home-bg-3.png');background-repeat:no-repeat;background-size:100%;background-position:right;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row style="color:#DD2866;font-size:24px;margin-top:40px;">
            {{ $t('poolSentence2') }}
          </el-row>

          <el-row style="font-size:16px">
            {{ $t('poolSentence3') }}
          </el-row>
          <el-row type="flex" justify="center" style="margin-top:40px;">
            <el-col :xs="24" :sm="12" style="border: 2px dashed #E91E63;">
              <img src="money.png">
            </el-col>

          </el-row>
          <el-row type="flex" justify="center" style="font-size:32px;margin-top:20px;margin-bottom:30px;">
            ${{ poolAwardMoney }}
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin-top:40px;padding-bottom:200px;width:100%;background-image:url('home-bg-4.png');background-repeat:no-repeat;background-position:right bottom;width:100%;" class="partner-background">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row type="flex" justify="center"  style="margin-top:40px">
            <el-col :xs="24" :md="22" :xl="22" style="line-height:40px;">
              <el-row style="text-align: left;font-size:30px;font-weight: 500;margin-left:10px;">
                {{$t('poolStat') }}
              </el-row>
              <div style="width:100%;height: 1px;background-color:  #E91E63;margin-top:20px;"></div>

              <div class="stat-shadow-border" style="margin-top:40px;margin-bottom: 40px;font-size:18px;">
                <el-row style="padding-top:30px;">
                  <el-col :span="8" :offset="1" style="text-align: left;">
                  {{$t('poolSentence4') }}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    {{ poolBalance }}PCFX
                  </el-col>
                </el-row>
                <el-row >
                  <el-col :span="8" :offset="1" style="text-align: left;">
                  {{$t('poolSentence5')}}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    {{ reserveTotalSupply }} PCFX
                  </el-col>
                </el-row>
                <el-row >
                  <el-col :span="8" :offset="1" style="text-align: left;">
                    {{$t('poolSentence6')}}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    10%
                  </el-col>
                </el-row>
                <el-row >
                  <el-col :span="8" :offset="1" style="text-align: left;">
                    {{$t('poolSentence7')}}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    Conflux Internal Interest
                  </el-col>
                </el-row>
                <div style="width:100%;height: 1px;background-color:  #E91E63;margin-top:20px;"></div>
                <el-row >
                  <el-col :span="22" :offset="1" style="text-align: left;">
                  </el-col>
                </el-row>
                <el-row >
                  <el-col :span="8" :offset="1" style="text-align: left;padding-top:10px;">
                    {{ $t('poolSentence9') }}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    {{ daliyPoolGo}} POOLGO
                  </el-col>
                </el-row>
                <el-row >
                  <el-col :span="8" :offset="1" style="text-align: left;">
                    {{ $t('poolSentence10') }}
                  </el-col>
                  <el-col :span="14" style="text-align: right;padding-bottom: 20px;">
                    {{pcfxApy}}%
                  </el-col>
                </el-row>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin-top:-150px">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row type="flex" justify="center"  style="margin-top:40px">
            <el-col :xs="24" :md="22" :xl="22" style="line-height:40px;">
              <el-row style="">
                <el-col :span="12" style="font-weight: 500;font-size:30px;text-align: left;padding-left:10px;">
                  {{ $t('poolSentence11') }}
                </el-col>
                <el-col :span="12" style="font-size:14px;text-align: right;margin-top:10px;padding-right:10px;">
                  {{ $t('poolSentence12') }}
              </el-col>
              </el-row>
              <div style="width:100%;height: 1px;background-color:  #E91E63;margin-top:20px;"></div>

              <div class="stat-shadow-border" style="margin-top:40px;margin-bottom: 40px;font-size:18px;">
                <el-row style="padding-top:30px;font-size:22px;font-weight: 600;color:#DD2866">
                  <el-col :span="8" :offset="1" style="text-align: left;">
                    {{ $t('poolSentence13') }}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    ${{ poolAwardMoney }}
                  </el-col>
                </el-row>
                <el-row v-if="awardInfoArray.length==0" style="text-align: center;">
                  {{ $t('poolSentence14') }}
                </el-row>
                <el-row v-else>
                <el-row v-for="(awardInfo,i) in awardInfoArray" :key="i">
                  <el-col :span="8" :offset="1" style="text-align: left;">
                    {{awardInfo.winner}}
                  </el-col>
                  <el-col :span="14" style="text-align: right">
                    ${{awardInfo.amount}}
                  </el-col>
                </el-row>
                </el-row>

              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row style="margin:0 auto;margin-top:40px;margin-bottom:100px;color:#999999">
      {{$t('moneyMore')}}
    </el-row>

    <vs-dialog :loading="depositDialogLoading" width="300px" not-center prevent-close v-model="depositDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('poolsSentence3part1')}} <b>{{$t('poolsSentence3part2')}} </b> {{$t('poolsSentence3part3')}}
        </h4>
      </template>


      <div class="con-content">
        <el-row  v-if="isNeedApprove" style="font-size: 12px;">{{ $t('noticeApproveContent') }}</el-row>

        <vs-input  icon-after v-else v-model="depositPoolMoney" :placeholder="$t('noticeOwnPcfx')+pcfxNumber.toFormat(2)" type="number">
          <template #icon>
            <vs-button @click="maxDepositPoolMoney()" circle flat
                       warn
            >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button v-if="isNeedApprove" @click="confirmApprove" transparent>
            {{$t('noticeApprove')}}
          </vs-button>
          <vs-button v-else @click="confirmDeposit" transparent>
            {{$t('noticeDeposit')}}
          </vs-button>
          <vs-button @click="depositDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>


    <vs-dialog :loading="withdrawPoolDialogLoading" width="300px" not-center prevent-close v-model="withdrawPoolDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeWithdraw')}} <b>PCFX</b>
        </h4>
      </template>


      <div class="con-content">
        <vs-input icon-after v-model="withdrawPoolMoney" :placeholder="$t('noticeWithdrawPcfx')+ticketNumber.toFormat(2)" type="number">
          <template #icon>
            <vs-button @click="maxWithdrawPoolMoney" circle flat
                       warn
            >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="showTimelockDialog" transparent>
            {{$t('noticeCountdownWithdraw')}}
          </vs-button>
          <vs-button @click="showImmediateDialog" transparent>
            {{$t('noticeNowWithdraw')}}
          </vs-button>
          <vs-button @click="withdrawPoolDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
    <vs-dialog :loading="timelockDialogLoading" width="550px" not-center prevent-close v-model="timelockDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeSureCountdownWithdrawPart1')}}<b> {{$t('noticeSureCountdownWithdrawPart2')}}</b>
        </h4>
      </template>


      <div class="con-content">
        <p>
          {{$t('noticeCountdownWithdrawInfo')}}
        </p>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="withdrawWithTimelockFrom" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="timelockDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog :loading="immediateDialogLoading" width="550px" not-center prevent-close v-model="immediateDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeSureNowWithdrawPart1')}}<b>{{$t('noticeSureNowWithdrawPart2')}}</b>
        </h4>
      </template>


      <div class="con-content">
        <p style="text-align:left">
          {{$t('noticeNowWithdrawInfo')}}
        </p>
        <vs-input v-model="maximumExitFee" :placeholder="$t('noticeMaxWithdrawFee')+(withdrawPoolMoney*0.01).toFixed(2)" type="number"></vs-input>

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="withdrawInstantlyFrom" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="closeImmediateDialog" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </el-main>

</template>


<script>
import config from "@/config";
import {BigNumber} from "bignumber.js";
import {Conflux} from "js-conflux-sdk";
import utils from "@/utils/utils";
import {mapActions} from "vuex";


export default {
  name: "pool",
  components: {
  },
  data() {
    return {
      poolBalance: 0,
      poolAwardBalance: 0,
      poolAwardMoney:0,
      poolMoney:0,
      prizePeriodRemainingSeconds: 0,
      windowHeight: 0,
      windowWidth: 0,
      depositDialogShow: false,
      depositPoolMoney: '',
      withdrawPoolDialogShow: false,
      withdrawPoolMoney: '',
      withdrawPoolMoneyTimeLock: "",
      immediateDialogShow: false,
      timelockDialogShow: false,
      maximumExitFee: '',
      withdrawPoolDialogLoading:false,
      depositDialogLoading: false,
      timelockDialogLoading:false,
      immediateDialogLoading:false,
      pcfxNumber: new BigNumber(0),
      ticketNumber:new BigNumber(0),
      awardInfoArray:[],
      pcfxApy:0,
      daliyPoolGo:0,
      reserveTotalSupply:0,
      intervalTimer:null,
      isNeedApprove:false,
      isFirstRun:true,
      dripRatePerSecond:0,
    };
  },
  computed: {
    pcfxContract: function () {
      return this.$store.state.web3.pcfxContract;
    },
    ticketContract: function () {
      return this.$store.state.web3.ticketContract;
    },
    poolGoContract: function () {
      return this.$store.state.web3.poolGoContract;
    },
    prizePoolContract: function () {
      return this.$store.state.web3.prizePoolContract;
    },
    periodicPrizeStrategyContract: function () {
      return this.$store.state.web3.periodicPrizeStrategyContract;
    },
    tokenFaucetContract: function () {
      return this.$store.state.web3.tokenFaucetContract;
    },
    cfxStakeContract: function () {
      return this.$store.state.web3.cfxStakeContract;
    },
    firstPoolColor: function () {
      if (this.choosePool == 0) {
        return '#E91E63'
      } else
        return '#FFFFFF'
    },
    secondPoolColor: function () {
      if (this.choosePool == 1) {
        return '#E91E63'
      } else
        return '#FFFFFF'
    },
    countdownTimerWidth: function () {
      console.log(this.windowWidth);
      if (this.windowWidth < 500) {
        return this.windowWidth / 5
      } else if (this.windowWidth <= 1200) {
        return this.windowWidth / 5
      } else {
        return this.windowWidth / 12
      }
    },
    countdownTimerPadding: function () {
      if (this.windowWidth < 500) {
        return 3
      } else if (this.windowWidth <= 768) {
        return 8
      } else {
        return 10
      }
    }
  },
  created() {
    const that = this;
    that.windowHeight = parseInt(`${window.innerHeight}`);
    that.windowWidth = parseInt(`${window.innerWidth}`);
    window.onresize = () => {
      return (() => {
        that.windowHeight = parseInt(`${window.innerHeight}`);
        that.windowWidth = parseInt(`${window.innerWidth}`);
      })();
    }
  },

  async mounted() {

    const that = this;
    that.prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())
    that.reserveTotalSupply = (await that.prizePoolContract.reserveTotalSupply()).toFormat(0);
    that.$nextTick(function() {
      that.$refs.timer.updateTime(that.prizePeriodRemainingSeconds.toNumber());
    })
    let awardInfoArray = await that.prizePoolContract.getFiveAwardInfo();
    that.awardInfoArray=[];
    // console.log(awardInfoArray);
    if (awardInfoArray !=null) {
      for (let i = 0; i < awardInfoArray.length; i++) {
        that.awardInfoArray.push({
          amount: new BigNumber(awardInfoArray[i].amount).multipliedBy(that.$store.state.web3.pcfxPrice).toFormat(0),
          winner: utils.shortAddr(awardInfoArray[i].addr)
        });
      }
    }

    let dripRatePerSecond = await that.tokenFaucetContract.dripRatePerSecond();
    that.dripRatePerSecond = dripRatePerSecond;
    that.daliyPoolGo = dripRatePerSecond.multipliedBy(60 * 60 * 24).toFormat(0);
    that.update();
    that.intervalTimer = setInterval(that.update, 6000)
  },
  beforeDestroy() {
    var that = this;
    clearTimeout(that.intervalTimer);
  },
  methods: {
    ...mapActions(['cfxLogin']),
    async update() {

      var that = this;
      var totalFunds = await that.cfxStakeContract.balanceOfUnderlyingView();
      var poolBalance = await that.prizePoolContract.accountedBalance();

      that.prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())

      let interest = totalFunds.multipliedBy(that.prizePeriodRemainingSeconds.multipliedBy(0.0408).div(31536000));
      // console.log("tickets" + tickets)
      var award = totalFunds.plus(interest).minus(poolBalance);
      let poolAwardMoney = award.multipliedBy(that.$store.state.web3.pcfxPrice);
      let poolMoney = poolBalance.multipliedBy(that.$store.state.web3.pcfxPrice);

      that.poolAwardBalance = award.toFormat(0);
      that.poolBalance = poolBalance.toFormat(0);

      that.poolAwardMoney = poolAwardMoney.toFormat(0);
      that.poolMoney = poolMoney.toFormat(0);
      that.pcfxApy = (new BigNumber(that.dripRatePerSecond * 60 * 60 * 24*365))
          .multipliedBy(that.$store.state.web3.poolgoPrice).div(poolBalance.multipliedBy(that.$store.state.web3.pcfxPrice)).multipliedBy(100).toFormat(1);

    },

    async showWithdrawDialog() {
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }

      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return ;
      }

      that.withdrawPoolDialogShow = true;
      that.withdrawPoolDialogLoading = true;

      that.ticketNumber =  (await that.ticketContract.balanceOf(that.$store.state.web3.account));
      that.withdrawPoolDialogLoading = false;
      that.withdrawPoolMoney = '';

    },
    showTimelockDialog() {

      var that = this;
      if (that.withdrawPoolMoney == '' || that.withdrawPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      this.withdrawPoolDialogShow = false;
      this.timelockDialogShow = true;
    },
    showImmediateDialog() {

      var that = this;
      if (that.withdrawPoolMoney == '' || that.withdrawPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      this.withdrawPoolDialogShow = false;
      this.immediateDialogShow = true;
      this.maximumExitFee = '';
    },
    closeImmediateDialog(){
      this.withdrawPoolDialogShow = true;
      this.immediateDialogShow = false;
    },
    closeTimeoutDialog(){
      this.withdrawPoolDialogShow = true;
      this.tiemoutDialogShow = false;
    },
    async withdrawWithTimelockFrom() {
      let that = this;
      that.timelockDialogLoading =true;
      let status = await that.prizePoolContract.withdrawWithTimelockFrom(that.$store.state.web3.account, that.$store.state.web3.account, that.withdrawPoolMoney, config.ticketAddress);
      that.timelockDialogLoading =false;
      if (status.succ == true) {
        console.log(status);
        let endBlockTime = new BigNumber(status.decodeLogs[status.decodeLogs.length - 1].object.unlockTimestamp).toNumber();

        let blockStatus = await this.$store.state.web3.confluxReader.getBlockByHash(status.blockHash);
        let seconds = endBlockTime - blockStatus.timestamp;
        console.log(seconds)
        let formatTime = utils.formatSeconds(seconds);
        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text:  that.$t('noticeSentece2Part1') + formatTime + that.$t('noticeSentece2Part2'),
        })
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
        return;
      }

      that.withdrawPoolDialogShow = false;
      that.timelockDialogShow = false;
      that.updateCfxAndPcfxBalance();
    }
    ,
    async withdrawInstantlyFrom() {
      let that = this;

      if (that.maximumExitFee == '' || that.maximumExitFee <0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      that.immediateDialogLoading =true;
      let status = await that.prizePoolContract.withdrawInstantlyFrom(that.$store.state.web3.account, that.withdrawPoolMoney, config.ticketAddress, that.maximumExitFee);
      that.immediateDialogLoading =false;
      console.log(status)
      if (status.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece3'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })

        return;
      }

      that.withdrawPoolDialogShow = false;
      that.immediateDialogShow = false;
      that.updateCfxAndPcfxBalance();
    },
    async showDepositDialog() {


      this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: this.$t('error'),
          text: this.$t('withdrawYourFund'),
        })

      return
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }


      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return;
      }
      that.depositDialogShow = true;
      that.depositDialogLoading = true;

      let curAllowance = await that.pcfxContract.allowance(this.$store.state.web3.account, config.prizePoolAddress);
      if (curAllowance.isLessThan(1000000000000)) {
        that.isNeedApprove = true;
      }
      that.pcfxNumber = (await that.pcfxContract.balanceOf(that.$store.state.web3.account));
      that.depositDialogLoading = false;
      that.depositPoolMoney ='';
    }
    ,

    async confirmApprove(){
      let that = this;

      that.depositDialogLoading = true;
      let curAllowance = await that.pcfxContract.allowance(this.$store.state.web3.account, config.prizePoolAddress);
      if (curAllowance.isLessThan(1000000000000)) {
        let status = await that.pcfxContract.approveMax(this.$store.state.web3.account, config.prizePoolAddress);

        that.depositDialogLoading = false;
        console.log(status)

        if (status.succ == true) {
          that.isNeedApprove = false;
        } else {

          this.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: status.message,
          })

          return;
        }
      }
      that.depositDialogLoading = false;

    }
    ,
    async confirmDeposit() {
      let that = this;

      if (that.depositPoolMoney == '' || that.depositPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      that.depositDialogLoading = true;

      let depositStatus = await that.prizePoolContract.depositTo(this.$store.state.web3.account, this.$store.state.web3.account, that.depositPoolMoney, config.ticketAddress, 'CFXTEST:TYPE.NULL:AAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA6F0VRCSW');

      that.depositDialogLoading = false;
      console.log(depositStatus)
      if (depositStatus.succ == true) {
        that.depositDialogShow = false;

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece4'),
        })
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: depositStatus.message,
        })
        return;
      }

      that.updateCfxAndPcfxBalance();
    }
    ,

    async updateCfxAndPcfxBalance(){
      let that = this;
      let pcfxBalance = await that.pcfxContract.balanceOf(that.$store.state.web3.account);
      that.$store.commit('UPDATE_PCFX_BALANCE', pcfxBalance);
      let balance = await this.$store.state.web3.confluxReader.getBalance(that.$store.state.web3.account)
      that.$store.commit('UPDATE_BALANCE', BigNumber(balance).div(BigNumber(10 ** 18)))

    },

    maxWithdrawPoolMoney(){
      let that = this;
      that.withdrawPoolMoney = that.ticketNumber.toFixed(2);
    },
    maxDepositPoolMoney(){
      let that = this;
      that.depositPoolMoney = that.pcfxNumber.toFixed(2);
    },
  },
};
</script>

